import { computed, defineComponent, onMounted, ref } from 'vue';
import { useStore } from '@/store';
import { pageContentConfig } from './config/pageContentConfig';
import pageContent from '@/components/page-content';
export default defineComponent({
  name: 'menus',
  components: {
    pageContent: pageContent
  },
  setup: function setup() {
    var msg = ref('msg in app');
    var store = useStore();
    onMounted(function () {
      store.dispatch('menuModule/queryMenuAction');
    });
    var list = computed(function () {
      return store.state.menuModule.list;
    });
    var totalCount = computed(function () {
      return store.state.menuModule.totalCount;
    });
    var listData = ref({
      list: list,
      totalCount: totalCount
    });
    return {
      pageContentConfig: pageContentConfig,
      listData: listData
    };
  }
});